import { Injectable } from '@angular/core';
import { Api } from '../api/api';
import { LoadingController } from 'ionic-angular';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class PromotionsProvider {
  apiURL = 'http://localhost:3000/';
 
  constructor(public http: HttpClient, private transfer: FileTransfer) { }
 
  getImages() {
    return this.http.get('https://sleepy-bastion-56260.herokuapp.com' + 'images').map(res => res);
  }
 
  deleteImage(img) {
    return this.http.delete('https://sleepy-bastion-56260.herokuapp.com' + 'file/all' + img._id);
  }
 
  uploadImage(img, desc) {
 
    // Destination URL
    let url = this.apiURL + 'images';
 
    // File for Upload
    var targetPath = img;
 
    var options: FileUploadOptions = {
      fileKey: 'image',
      chunkedMode: false,
      mimeType: 'multipart/form-data',
      params: { 'desc': desc }
    };
 
    const fileTransfer: FileTransferObject = this.transfer.create();
 
    // Use the FileTransfer to upload the image
    return fileTransfer.upload(targetPath, url, options);
  }
}


